	module.exports = {"data":{"markdownRemark":{"html":"<p>We released Version 0.1a <a href=\"/tag/flan\">of the Framework of LunAtic Notation, an</a> eastward barrack style framework library. With this update, we have greatly rearranged and added the standard bullet function and internal division counter function.</p>\n<h2>17:45 Addendum</h2>\n<p>We released version 0.1b <a href=\"/tag/flan\">of the Framework of LunAtic Notation, an</a> eastward barrack style framework library. Various features added at 0.1a were variously packed full of bugs, so we fixed a lot. We also installed two new functions. By the way, if you notice, the summer comic is another week, so I will try to organize the distribution list again. <a href=\"/!/thA/\">It</a> is the sequel STG of the <a href=\"/!/thA/\">sealed castle</a> of <a href=\"/!/thA/\">Toho</a> . It is under construction for the completion of the next winter comic C 71, but it is an updated version of the one distributed at the <a href=\"http://www.reitaisai.com/\">Hakurei Shrine example</a> large <a href=\"http://www.reitaisai.com/\">festival 3</a> with its trial version. <a href=\"/!/thC/\">Toho Dream Theater Drama ~ Conceal the Conclusion Details here</a> . Distribution price is 100 yen. We will distribute free jacket of trial version A (example festival version) for free. (However, please note that it will be in the form of a simple jacket exchange.) Also, I will play the demonstration replay movie from around 11:20 on the day (I'd like to play it from the start but my notebook PC is a battery Because it is not good to have flattery orz)</p>\n<h2>(Entrusted) Toho key holder</h2>\n<p>Click here for details of Tohoku Key Holder . 30 pieces limited, distribution price will be 500 yen. It seems that there is no current resale for this burst of dissemination.</p>\n<h2>Leila Prismriver</h2>\n<p>It will become a resale of last winter comic C69. <a href=\"/!/leila/\">Leila Prismriver details here</a> . Distribution price is 300 yen.</p>\n<h2>Toei Character A5 Ramika</h2>\n<p>We will offer 1 free of charge to those who purchase the above three at the same time. It will be finished as soon as it is lost, with only the first-come first-served basis. There are three kinds of patterns: Youmu (3), Marisa (2), Secret Club (2). (It was distributed at the <a href=\"http://www.h4.dion.ne.jp/%7Ewashoi/\">All -</a> Jun - <a href=\"http://www.h4.dion.ne.jp/%7Ewashoi/\">dong sale party '</a> Watcheshi' on <a href=\"http://www.h4.dion.ne.jp/%7Ewashoi/\">March</a> 26th 2006)</p>\n<h2>Trial version A2 → Update trial version B</h2>\n<ul>\n<li>Scenario increased from 2 routes to 4 routes</li>\n<li>Together BGM has increased to 13 songs from 13 songs (eg 15 festival versions) to 25 songs</li>\n<li>New bonus item (At present it is just a bonus item, but in the full version it will be an important item on the story)</li>\n<li>We have adjusted the difficulty level of the old scenario A / C</li>\n<li>We adjusted the last spelling condition</li>\n</ul>","frontmatter":{"cover":null,"date":"2006-08-06T00:00:00+09:00","strDate":"2006/8/5","title":"Summer comic details"}}},"pathContext":{"path":"/en/blog/2006/08/06-夏コミ詳細/","langKey":"en"}}

